import React, { useCallback, useEffect, useRef, useState } from "react";
import { ScrollContainerContext } from "./ScrollContext";
import environment from "./utils/environment";

interface IProps {
  children: React.ReactNodeArray;
  scrollParent: Window | HTMLElement;
}

interface IState {
  currentY: number;
  viewportHeight: number;
  totalPage: number;
  totalHeight: number;
  totalProgress: number;
  realPage: number;
  currentPage: number;
  currentProgress: number;
}

const ScrollAnimatorContainer = ({ children, scrollParent = window }: IProps) => {

  const [scrollData, setScrollData] = useState<IState>({
    currentY: 0, // ÌòÑÏû¨ Ïä§ÌÅ¨Î°§ ÏúÑÏπò(px)
    viewportHeight: 0, // ÌôîÎ©¥ ÎÜíÏù¥(px)
    totalPage: 0, // Ï¥ù ÌéòÏù¥ÏßÄ Ïàò
    totalHeight: 0, // Ï¥ù ÌéòÏù¥ÏßÄ ÎÜíÏù¥ Ìï© (px)
    totalProgress: 0, // Ï¥ù ÌéòÏù¥ÏßÄ ÏßÑÌñâÎ•† (%)
    realPage: 0, // Ïã§Ïàò ÌéòÏù¥ÏßÄ
    currentPage: 0, // Ï†ïÏàò ÌéòÏù¥ÏßÄ
    currentProgress: 0, // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ ÏßÑÌñâÎ•† (%)
  });

  const scrollEvent = useCallback(() => {
    const currentY: number = scrollParent === window ? window.pageYOffset : (scrollParent as HTMLElement).scrollTop;
    const viewportHeight: number = scrollParent === window ? environment.height : (scrollParent as HTMLElement).clientHeight;
    const totalPage: number = children.length || 0;
    const totalHeight: number = totalPage * (viewportHeight - 1);
    const totalProgress: number = currentY / totalHeight; // Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄ ÏßÑÌñâÎ•† 0 ~ 1
    const realPage: number = currentY / viewportHeight; // Ïã§Ïàò ÌéòÏù¥ÏßÄ
    const currentPage: number = Math.floor(realPage); // Ï†ïÏàò ÌéòÏù¥ÏßÄ
    const currentProgress: number = realPage - currentPage; // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ ÏßÑÌñâÎ•†
    setScrollData({
      currentY,
      viewportHeight,
      totalPage,
      totalHeight,
      totalProgress,
      realPage,
      currentPage,
      currentProgress,
    } as IState);
  }, []);

  useEffect(() => {
    scrollEvent();
    scrollParent.addEventListener("scroll", scrollEvent);
    scrollParent.addEventListener("resize", scrollEvent);
    return () => scrollParent.removeEventListener("scroll", scrollEvent);
  }, []);

  return (
    <div style={{ margin: 0, padding: 0, userSelect: "none" }}>
      <ScrollContainerContext.Provider value={scrollData}>
        {children}
      </ScrollContainerContext.Provider>
    </div>
  );
};

export default ScrollAnimatorContainer;
